}

input, textarea {
  color: initial;
  letter-spacing: normal;
  word-spacing: normal;
  text-transform: none;
  text-indent: 0;
  text-shadow: none;
  text-align: start;
  margin-top: 0;
  line-height: normal;
  display: inline-block;
}

input:-webkit-autofill {
  -webkit-text-fill-color: inherit;
}

.menu {
  box-sizing: border-box;
  opacity: 0;
  pointer-events: none;
  background-color: #d12d35;
  height: 100vh;
  padding: 25px 20px 0;
  font-family: Cinzel, sans-serif;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  overflow-y: auto;
}

@media (width >= 744px) {
  .menu {
    padding: 72px 50px 0;
  }
}

@media (width >= 1260px) {
  .menu {
    padding: 50px 120px 0;
  }
}

.menu:target {
  opacity: 1;
  pointer-events: all;
}

.menu__close {
  background-image: url("Icon-Close.b0ea3ab9.svg");
  width: 24px;
  height: 24px;
}

.menu__close:hover {
  transition: all .3s;
  transform: scale(1.4);
}

.menu__top {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.menu__nav {
  width: 241px;
  margin-top: 40px;
}

.menu__nav__list {
  text-transform: uppercase;
  padding: 0;
  list-style: none;
  display: list-item;
}

.menu__nav__items {
  margin-bottom: 32px;
}

.menu__nav__link {
  color: #fff;
  letter-spacing: 2px;
  text-align: left;
  font-size: 22px;
  font-weight: 700;
  line-height: 22px;
  text-decoration: none;
  position: relative;
}

.menu__nav__link:after {
  content: "";
  transform-origin: 0;
  background-color: #fff;
  width: 100%;
  height: 1px;
  margin-bottom: 5px;
  transition: transform .3s;
  display: block;
  position: absolute;
  transform: scale(0);
}

.menu__nav__link:hover:after {
  transform: scaleX(1);
}

.menu__contacts {
  flex-direction: column;
  width: 201px;
  height: 28px;
  display: flex;
}

@media (width >= 1260px) {
  .menu__contacts {
    padding-top: 16px;
  }
}

.menu__numbers {
  color: #fff;
  text-align: left;
  flex-direction: column;
  padding-bottom: 8px;
  font-size: 16px;
  font-weight: 700;
  line-height: 27px;
  display: flex;
}

.menu__mobile {
  color: #fff;
  text-align: left;
  font-size: 16px;
  font-weight: 700;
  line-height: 27px;
}

.menu__mobile:hover {
  transform-origin: center;
  transition: all .3s;
  transform: scale(1.1);
}

.menu__call {
  color: #fff;
  letter-spacing: 2px;
  text-align: left;
  height: 16px;
  margin-top: 8px;
  font-size: 12px;
  font-weight: 700;
  line-height: 16px;
}

.menu__call:after {
  content: "";
  background-color: #fff;
  width: 126px;
  height: 1px;
  position: absolute;
  top: 540px;
  left: 20px;
}

@media (width >= 744px) {
  .menu__call:after {
    top: 605px;
    left: 50px;
  }
}

@media (width >= 1260px) {
  .menu__call:after {
    width: 120px;
    top: 600px;
    left: 120px;
  }
}

